@import "../../assets/scss/variables";

.token {
	&__information {
		.MuiCard {
			&-root {
				border: none;
				margin-bottom: 1.618em;
				max-width: 100%;
				position: relative;
				&::after {
					clear: both;
					content: " ";
					height: 1px;
					display: block;
				}
			}
			&Header {
				&-root {
					padding: 0.618em 0 !important;
					border-bottom: 1px solid var(--unikbase-gray);
				}
				&-avatar {
					margin-right: 8px;
				}
				&-title {
					font-size: 20px;
				}
				&-action {
					margin-top: auto;
					margin-bottom: auto;
					margin-right: 0;
					.MuiIconButton-root {
						font-size: 16px;
					}
				}
			}
			&-root.description .MuiCardHeader-title {
				font-size: 1.618em;
				margin-top: 0.618em;
			}
			&Content {
				&-root {
					padding: 1em 0;
					&:last-child {
						padding-bottom: 0;
					}
				}
			}
			&Actions {
				&-root {
					justify-content: right;
					.MuiIconButton-root {
						font-size: 16px;
						// border: 1px solid $gray;
						// border-radius: 4px;
					}
				}
			}
		}
	}
	&-document__thumbnail {
		width: 40px;
		display: inline-block;
		img {
			width: 100%;
			height: auto;
		}
	}
}
.attributes {
	padding: 1em 0 !important;
	.attribute {
		display: flex;
		flex-direction: row;
		width: calc(50% - 8px);
		flex-basis: calc(50% - 8px);
		margin-bottom: 8px;
		border-radius: 2.5em;
		border: 1px solid var(--unikbase-gray);
		justify-content: center;
		align-items: center;
		&:nth-child(2n) {
			margin-left: auto;
		}
		.avatar-with-icon {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			height: 5em;
			width: 5em;
			border-radius: 50%;
			background-color: var(--unikbase-gray);
			flex-shrink: 0;
			flex-grow: 0;
		}
		.content {
			flex-shrink: 1;
			flex-grow: 1;
			text-align: center;
		}
		&.length .avatar-with-icon {
			background-color: $lengthColor;
		}
		&.depth .avatar-with-icon {
			background-color: $depthColor;
		}
		&.width .avatar-with-icon {
			background-color: $widthColor;
		}
		&.weight .avatar-with-icon {
			background-color: $weightColor;
		}
	}
	@media screen and (max-width: 800px) {
		.attribute {
			height: 40px;
			.avatar-with-icon {
				width: 40px;
				height: 40px;
				font-size: 9px;
			}
			.content {
				font-size: 12px;
			}
		}
	}
}
.documents {
	.MuiListItem {
		&-root {
			padding: 0 0;
		}
		&-root.file-invalid .MuiListItemText-root button {
			color: $gray;
		}
	}
	.MuiIconButton-root {
		font-size: 14px;
	}
	> .MuiBox-root > .MuiBox-root {
		padding: 1em 0 !important;
	}
}
.document__preview {
	.document__viewer {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		height: 90vh;
		overflow: hidden;
		background-color: $gray;
		&.image {
			height: auto;
			width: 90%;
			width: 90vw;
			height: 90vh;
			text-align: center;
			background-color: transparent;
			> * {
				pointer-events: all;
			}
			div {
				background-repeat: no-repeat;
				background-size: contain;
				background-position: center;
			}
		}
		&--file {
			width: 90vw;
		}
		&--3D {
			width: 90vw;
			height: 100%;
		}
		.loading {
			position: absolute;
			top: calc(50% - 20px);
			left: calc(50% - 20px);
			z-index: 999;
		}
		.progress {
			position: absolute;
			bottom: 0;
			left: 0;
			z-index: 999;
			width: 100%;
			&.obj-loading-progress {
			}
			&.texture-loading-progress {
				bottom: 4px;
			}
		}
	}
	& &-close {
		color: #fff;
		position: fixed;
		right: 10px;
		top: 10px;
		font-size: 16px;
		min-width: auto;
		border: 1px solid $gray;
		z-index: 999;
	}
}
