@import '../../../assets/scss/flex';
@import '../../../assets/scss/variables';
.upload {
	&--drag-drop {
		@extend .flex;
		@extend .flex--vertical;
		@extend .flex--align-center;
		@extend .flex--justify-center;
		cursor: pointer;
		position: relative;

		border: 1px dashed $red;
		background-color: $gray;
		border-radius: 8px;
		min-height: 120px;

		input[type="file"] {
			display: none;
		}
		.drag__file-element {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
		}
	}
}