@import "variables";

@import "reset";
@import "fonts";
@import "colors";

@import "layout";
@import "flex";
@import "utils";


a {
	cursor: pointer;
    text-decoration: none;
    color: $red;
}

.page-anchor {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    line-height: 1.618em;
    margin: 0 auto;
    background: lighten($light-red , $amount: 10%);
    color: $black;
    font-weight: 300;
    width: fit-content;
    padding:0 1em 0 0;
    border-radius: calc(1.618em/2);
    span {
        width: 1.618em;
        height: 1.618em;
        background-color: darken($light-red, 10%);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        margin-right: 0.5em;
        svg {
            font-size: 0.718em;
        }
    }
}

.site {
    font-family: 'Rubik', sans-serif;
    box-sizing: border-box;
    background-color: $black;
    color: $white;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    padding: 2em 0;
    section {
        position: relative;
    }
    &.single {
        overflow:auto;
        min-height: 100vh;
        height: auto;
    }
    &.login {
        padding: 70px 0;
    }

    &__header {
        margin-bottom: 2em;
    }
}