@import "menu";
@import "form";

.m { // margin
    &--l {
        margin-left: auto;
    }
    &--t {
        margin-top: auto!important;
    }
}

.w {
    &--100 {
        width: 100%;
        flex-basis: 100%;
    }
    &--50 {
        width: 50%;
        flex-basis: 50%;
    }
}

.layer {
    &--bg {
        z-index: 1;
    }
    &--mid {
        z-index: 50;
    }
    &--top {
        z-index: 999;
    }
}

.align {
    &--center {
        text-align: center;
        justify-content: center;
    }
}

.image {
    &--responsive {
        width: 100%;
        height: auto;
    }
    &--responsive-vertical {
        height: 100%;
        width: auto;
    }
}

.list {
    &--circle {
        list-style-type: circle;
        list-style-position: inside;
    }
}
.list--number {
    list-style: none;
    li {
        margin-bottom: 1em;
        padding-left: 2.125em;
        position: relative;
        h3 {
            &::before {
                content: "0"counter(list-item)"\a0\a0";
                font-weight: 500;
                position: absolute;
                left: 0;
                top: 0;
            }
        }
    }
}