@import '../../assets/scss/variables';

.wallet {
    &__token-upload,
    &__overview {
        background-color: $white;
        border: 1px solid $gray;
        border-radius: 8px;
        padding: 1em;
        .description {
            flex-grow: 1;
            flex-shrink: 1;
            .MuiOutlinedInput-root {
                border-width: 1px;
            }
            &__content {
                color: $black;
            }
        }
        .avatar {
            margin-right: 1em;
        }
    }
    &__image {
        width: 100px!important;
        height: 100px!important;
    }

    &__tokens {
        .MuiCard {
            &Content-root {
                padding: 1em;
                max-width: calc(100% - 100px - 2em);
                word-wrap: break-word;
            }
            &Media-root {
                width: 100px;
                width: calc(100px + 2em);
            }
        }
        @media screen and (max-width: 800px) {
            .token {
                flex-direction: column;
                .MuiCard {
                    &Media-root {
                        width: 100%;
                        aspect-ratio: 4 / 3;
                    }
                    &Content-root {
                        width: 100%;
                        max-width: 100%;
                    }
                }
            }
        }
    }

    .description {
        max-width: 100%;
        word-break: break-all;
        .content {
            margin-right: 1em;
        }
        &__editor {
            &.editing .editor__actions {
                margin-bottom: 8px;
            }
            .editor__actions {
                button {
                    font-size: 12px;
                    &:hover {
                        border: none;
                    }
                }
            }
        }
        &:hover .editor__actions {
            display: block;
        }
    }
}

@media screen and (max-width: 767px) {
    .wallet {
        &__overview {
            flex-direction: column;
            .avatar {
                margin-right: 0;
                margin-bottom: 1em;
                .wallet__image {
                    margin-left: auto;
                    margin-right: auto;
                }
            }
            .description {
                margin-bottom: 1em;
            }
        }
    }
}