@import '../../assets/scss/variables';

$header-height: 95px;
$sidebar-width: 240px;
$icon-size: 24px;

.site {
    &--platform {
        background-color: $gray;
        color: $black;
        padding-top: $header-height;
        padding-left: $sidebar-width;
        padding-right: $header-height;
        padding-bottom: 0;
        transition: left;
        transition-timing-function: ease-in-out;
        transition-duration: 200ms;
        overflow-y: scroll;

        &-close {
            padding-left: $header-height;
        }
        > .container {
            padding-top: $header-height;
            width: calc(100% - $header-height);
            margin-left: $header-height;
        }


        .page {
            &__title {
                font-size: 32px;
                line-height: 32px;
                &::after {
                    margin-top: 28px;
                    margin-bottom: 48px;
                    content: " ";
                    display: block;
                    background-color: $black;
                    height: 2px;
                    width: 48px;
                }
            }
        }
    }
}

.platform {
    &__appbar {
        box-shadow: none!important;
        border: none;
        width: auto!important;
        right: 0;
        left: $sidebar-width!important;
        transition-property: left, width!important;
        transition-timing-function: ease-in-out!important;
        transition-duration: 200ms!important;
        &--close {
            left: $header-height!important;
        }
    }
    &__toolbar {
        display: flex;
        background-color: $white;
        color: $black;
        justify-content: flex-end;
        height: $header-height;
        padding-right: 0;
        .arrowdown-button {
            width: $header-height;
            height: $header-height;
            font-size: 12px;
            border-radius: 0;
            color: $black;
            &:hover {
                background-color: transparent;
            }
        }
    }
    
    &__sidebar {
        border: none;
        .MuiDrawer-paper {
            width: $sidebar-width;
            background-color: $black;
            color: $white;
            border: none;
            transition-property: width;
            transition-timing-function: ease-in-out;
            transition-duration: 200ms;
        }
        &--close {
            .MuiDrawer-paper {
                width: $header-height;
            }
            .platform__menu .icon {
                margin-right: 0!important;
            }
        }
        &-header {
            background-color: $red;
            color: $white;
            height: $header-height;
            flex-basis: $header-height;
            flex-shrink: 0;
            flex-grow: 0;
            .logo {
                width: 100%;
                height: $header-height;
            }
        }
        &-footer {
            display: flex;
            margin-top: auto;
            width: 100%;
            justify-content: center;
            .MuiIconButton-root {
                color: $white;
            }
        }
    }
    &__menu {
        padding-top: 35px!important;
        .MuiListItem {
            &-root {
                &:hover,
                &.active {
                    .icon {
                        background-color: $red;
                    }
                }
                .icon {
                    margin-right: 35px;
                    color: $white;
                    display: inline-block;
                    height: $icon-size;
                    width: $icon-size;
                    min-width: auto;
                    background-color: transparent;
                    background-repeat: no-repeat;
                    background-size: 90%;
                    -webkit-mask-repeat: no-repeat;
                    -webkit-mask-position: center;
                    -webkit-mask-size: contain;
                    mask-repeat: no-repeat;
                    mask-position: center;
                    mask-size: contain;
                    background-color: $white;
                    &--profile {
                        -webkit-mask-image: url('../../assets/icons/icon-profile.svg');
                        mask-image: url('../../assets/icons/icon-profile.svg');
                    }
                    &--wallet {
                        -webkit-mask-image: url('../../assets/icons/icon-wallet.svg');
                        mask-image: url('../../assets/icons/icon-wallet.svg');
                    }
                    &--others {
                        -webkit-mask-image: url('../../assets/icons/icon-others.svg');
                        mask-image: url('../../assets/icons/icon-others.svg');
                    }
                    &--transfer {
                        -webkit-mask-image: url('../../assets/icons/icon-transfer.svg');
                        mask-image: url('../../assets/icons/icon-transfer.svg');
                    }
                    &--dashboard {
                        -webkit-mask-image: url('../../assets/icons/icon-dashboard.svg');
                        mask-image: url('../../assets/icons/icon-dashboard.svg');
                    }
                }
            }
            &Button-root {
                padding-left: 35px;
                padding-right: 35px;
            }
        }
    }
}