.gallery {
	width: 100%;
    &__item {
        display: block;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        width: 100%;
        height: 300px;
        @media screen and (min-width: 600px) {
            height: auto;
			width: 100%;
			aspect-ratio: 16/9;
        }
    }
}