.form {
	&__signup {
		position: relative;
		.MuiInputLabel-root  {
			background-color: #fff;
			padding-left: 0.382em;
			padding-right: 0.382em;
		}
	}
	&--logging-in {
		display: block;
		height: 100%;
		width: 100%;
		top: 0;
		left: 0;
		background-color: rgba(255, 255, 255, 0.7);
		cursor: none;
		position: absolute;
		z-index: 999;
		
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
	}
}